              </div>
              <div class="cell">
                <a class="user_avatar pull-left" href="/user/wangfeng_my">
                  <img src="//gravatar.com/avatar/94baf776b3ab8b163c5932774dfaed0d?size=48" title="wangfeng_my">
                </a>
                <span class="reply_count pull-left">
                <span class="count_of_replies" title="回复数">1</span>
									<span class="count_seperator">/</span>
									<span class="count_of_visits" title="点击数">151</span>
              </span>
                <a class="last_time pull-right" href="/topic/596f03834357a3434f91f68e#5970772b4357a3434f91f6e5">
