      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data () {
      return {
        list: [],
        params: {
          page: 1,//页数
          limit: 16,//每一页的主题数量
        },
        /*loadmore*/
        loading: false,
        allLoaded: false,
        wrapperHeight: 0,
        scrollTop: 0
      }
    },
    watch: {
      '$route' (to, from) {
        // 对路由变化作出响应...
        //console.log(to.params.page);
        if (from.name == 'ArticleDetail') {
          this.$bus.emit('headerText', `文章列表`);
        }
      }
    },
    filters: {
      tab(val, val1){
        if (val == 'share' && val1) {
          return '置顶'
        } else if (val == 'share') {
          return '分享'
        } else if (val == 'ask') {
          return '问答'
        } else if (val == 'job') {
          return '招聘'
        } else {
          return 'others'
        }
      }
    },
    mounted(){
      this.$bus.emit('headerText', `文章列表`);
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      this.getArticleList();
    },
    methods: {
      getArticleList(){
        var self = this;
        self.$http.get(self.$CONSTANTS.APIArticleList, {params: self.params}).then((res) => {
          //console.log(res);
          if (res.body.data.length) {
            //有长度触发无线滚动
            self.loading = false;
            res.body.data.forEach((item, index) => {
              self.list.push(item);
            });
            //console.log(self.list);
          } else {
            //数组长度为0表示已经全部加载完
            self.allLoaded = true;
          }
        }, (res) => {
          console.log(res)
        });
      },
      loadMore() {
        //this.showCoins = true;
        var self = this;
        self.loading = true;
        self.params.page += 1;
        self.getArticleList();
      },
      scroll() {
        this.scrollTop = this.$refs.wrapper.scrollTop
      },
      /* activated() {
       this.$refs.wrapper.scrollTop = this.scrollTop
       }*/
    }

  }
</script>


<style scoped>
  .cell {
    overflow: hidden;
    position: relative;
    padding: .2rem 0 .2rem .2rem;
    font-size: .28rem;
  }

  .panel .cell {
    padding-right: .2rem;
    background: #fff;
    border-bottom: 1px solid #f0f0f0;
  }

  .panel .cell:last-of-type {
    border-bottom: none;
  }

  .panel .inner {
    line-height: 2em;
    padding: .2rem;
    background-color: #fff;
    border-radius: 0 0 .06rem .06rem;
  }

  .panel .inner a {
    color: #778087;
  }

  .pull-left {
    float: left;
  }

  .pull-right {
    float: right;
  }

  .user_avatar img, .user_big_avatar img {
    width: .6rem;
    height: .6rem;
    border-radius: .06rem;
  }

  #content .reply_count {
    position: absolute;
    bottom: 0;
    left: 1.7rem;
    text-align: left;
    line-height: 2em;
    font-size: .2rem;
  }

  .cell .reply_count {
    width: 1.4rem;
    display: inline-block;
    text-align: center;
  }

  .cell .count_of_replies {
    color: #9e78c0;
  }

  .cell .count_seperator {
    margin: 0 -.06rem;
    font-size: .2rem;
  }

  .cell .count_of_visits {
    font-size: .2rem;
    color: #b4b4b4;
  }

  .last_time .user_small_avatar {
    height: .36rem;
    width: .36rem;
    vertical-align: middle;
    margin-right: .5em;
    border-radius: .6rem;
  }

  .last_time .last_active_time {
    text-align: right;
    min-width: 1rem;
    display: inline-block;
    white-space: nowrap;
  }

  .topic_title_wrapper {
    padding-left: .8rem;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .put_top {
    background: #369219;
    padding: .04rem .08rem;
    border-radius: .06rem;
    -webkit-border-radius: .06rem;
    -moz-border-radius: .06rem;
    -o-border-radius: .06rem;
    color: #fff;
    font-size: .24rem;
  }

  #topic_list {
    overflow: auto;
  }

  #topic_list a.topic_title {
    color: #333;
  }

  a.topic_title {
    max-width: 70%;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    font-size: .32rem;
    line-height: .6rem;
  }

  .topiclist-tab {
    background-color: #e5e5e5;
    color: #999;
    padding: .04rem .08rem;
