<template>
  <div class="mt-96 w100">
    <div v-html="detailInfo.content"></div>
  </div>
</template>

<script>
  export default {
    name: '',
    data () {
      return {
        detailInfo: {}
      }
    },
    mounted(){
      this.$bus.emit('headerText', `${this.$route.query.title}`);
      this.getDetail();
    },
    destroyed(){
      this.$bus.emit('headerText', ``);
    },
    methods: {
      getDetail(){
        this.$http.get(`${this.$CONSTANTS.APIArticleDetial}/${this.$route.params.id}`).then((res) => {
          this.detailInfo = res.body.data;
        }, (res) => {
          console.log(res);
        });
      }
    }
  }
</script>


<style scoped>

</style>



